.luxury {
    max-width: 1040px;
    margin: auto;
    padding: 5rem 1rem;
}

.luxury .container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
    padding: 4rem 0;
}

.heading h1 {
    font-size: 4rem;
    text-align: center;
    padding: 2rem;
}

.heading p {
    text-align: center;
}

.text_bg {
    background: rgba(171, 197, 255, 0.502);
    height: 30px;
    max-width: 650px;
    margin: auto;
    position: absolute;
    left: 0;
    right: 0;
}

.text_bg p {
    font-size: 1.3rem;
    font-weight: 600;
    margin-top: -0.6rem;
}

.card {
    width: 100%;
    border-radius: 10px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

.card img {
    width: 100%;
    height: 250px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    object-fit: cover;
}

.card .content {
    text-align: center;
    padding: 1rem;
    font-size: 1rem;
}

@media screen and (max-width: 768px) {
    .luxury .container {
        grid-template-columns: repeat(2, 1fr);
    }

    .heading h1 {
        font-size: 3rem;
        text-align: center;
        padding: 2rem;
    }


}


@media screen and (max-width: 500px) {
    .luxury .container {
        grid-template-columns: 1fr;
    }
}