.hero {
    background: url('https://images.unsplash.com/photo-1560253787-9c3babc1fab2?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8MzF8fHxlbnwwfHx8fA%3D%3D&auto=format&fit=crop&w=500&q=60') no-repeat center/cover;
    display: flex;
    width: 100%;
    height: 500px;
}

form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 700px;
    width: 100%;
    margin: auto;
    margin-top: 10%;
    padding: 6px 15px;
    background-color: rgba(247, 247, 247, 0.9);
    box-shadow: rgba(0, 0, 0, 0.9) 0px 8px 24px;
    border-radius: 25px;
}

form label {
    font-size: .8rem;
    padding-bottom: 2px;
}

.text, .from, .until {
    display: flex;
    flex-direction: column;
}

.from, .until {
    border-left: 1px solid #333;
    padding-left: 6px;
}

form input {
    background-color: transparent;
    border: none;
    font-family: sans-serif;
}

form input:focus {
    outline: none;
}

form .text_input {
    width: 300px;
    font-size: 1rem;
}

.search_btn {
    display: flex;
    align-items: center;
}

.search_btn button{
    display: none;
}

@media screen and (max-width: 720px) {
    form {
        display: flex;
        flex-direction: column;
        max-width: 100%;
        margin: auto 1rem;
    }

    .text, .from, .until {
        width: 100%;
        padding: .2rem;
    }

    .from, .until {
        border-left: none;
    }

    .text_input {
        font-size: .8rem;
    }

    .border {
        border-top: 1px solid #333;
        padding: 8px;
    }

    form label {
        padding: .4rem 0;
    }

    .search_btn {
        width: 100%;
        border-top: 1px solid #333;
        padding: 8px 0;
    }

    .search_btn .btn {
        display: block;
        background-color: #593cfb;
        color: #fff;
        width: 100%;
        font-weight: 600;
        font-size: 1rem;
        border: none;
        padding: 12px;
        margin: .5rem 0;
        cursor: pointer;
        border-radius: 35px;
    }

    .search_btn .btn:hover {
        background-color: #4733b7;
        transition: background-color 1s;
    }

    .icon {
        display: none;
    }
}